@import "core/variables";
@import "core/reset";
@import "layout/page";
@import "layout/header";
@import "layout/footer";

:root {
	font-family: $font-main;
}
body {
	background-color: rgb(255, 210, 156);
	margin: 0;
	padding: 0;
}
.headerbox{
    width: 100vw;
    height: 420px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url("../images/backimage.jpeg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}


.title {

		text-transform: uppercase;
		color:white;
		font-size: medium;
		text-align: center;
		margin-bottom: 30px;
		background-color:#ad4601;
	
	}


.searchbox {
	border-radius: 5px;
    position: absolute; 
    top:400px;
    z-index: 100;
	box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
	margin: 0px 30px 30px 30px;
	height: 40px;
	background-color: white;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 20px;
	padding: 20px;
}
.inputSearch {
	width: 85vw;
	border: solid 1px #ad4601;
}
.searchbtn {
	padding: 5px 10px 5px 10px;
	color: white;
	background-color:#ad4601;
	display: inline-block;
	border: 0;
	border-radius: 10px;
}
.superbigbox{
	display: flex;
	flex-direction: row;
	gap: 70px 100px;
	margin-top:  90px;
	justify-content:center;


}
.list {
	list-style: none;
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	gap: 30px;


}
.resetbutton{
	transform: translateY(45px);
	height: 60px;
	display: flex;
	justify-content: center;
	align-items: center;
	&__btn{
		text-transform: uppercase;
		
		font-size:medium;
		padding: 10px 10px 10px 10px;
	color: white;
	font-weight: 700;

	background-color:#ad4601;
	display: inline-block;
	border: 0;
	border-radius: 10px;
	}	
}
.listfav {
	width: 420px;
	list-style: none;
	grid-template-columns: 1fr 1fr;
	display: grid;
	gap: 30px;
}
.card {
	padding: 20px;
	background-color: white;
	border-radius: 5px;
	width: 190px;
	height: 320px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
}

.selected {
	background-color: #f8b425;
}
.photobox {
	border-radius: 5px;
	overflow: hidden;
	object-fit: cover;
	height: 220px;
	width: 150px;
	align-self: center;
}
.card_name {
	font-size: 14px;
	font-weight: 600;
	text-align: center;
}
.card_status {
	font-size: 12px;
	font-weight: 300;
	text-align: center;
}
.favoritesList {
	height: 300px;
}

.card_img {
	align-self: center;
	height: 100%;
}
.charactesrList {
	height: 300px;
}
.collapsed{
	display: none;
}
